@import url(https://fonts.googleapis.com/css?family=Patrick+Hand);
/* @import url('https://fonts.googleapis.com/css?family=Quicksand:500'); */

body {
  margin: 0;
  padding: 0;
  font-family: 'Patrick Hand', cursive;
  padding-bottom:65px;
}

.navbar-container{
    font-size: 1.3rem;
}
.navbar-brand{
    font-size: 1.5rem;
}
.navbar-dark .navbar-nav .nav-link {
    color: white;
 
}
.Footer{
    text-align: center;
    position: fixed;
    height: 60px;
    bottom: 0;
    width: 100%;
    color: white;
    background-color:#343a40;
    display: block;
}

.fab{
    font-size: 30px;
    margin-left: 15px;
    line-height: 2;
}

.Footer a {
    text-decoration: none;
    color: #fff;
}

.Home{
    text-align: center;
}

.Home-title{
    position: relative;
    -webkit-animation-name: title;
            animation-name: title;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.details__headtitle{
    margin-bottom: 15px;
}

.react-logo{
    width: 148px;
    position: relative;
    -webkit-animation-name: react-logo;
            animation-name: react-logo;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-fill-mode:forwards;
            animation-fill-mode:forwards;
}


@-webkit-keyframes react-logo{
    0%{left:-130px;}
    100%{left:-15px;}
}


@keyframes react-logo{
    0%{left:-130px;}
    100%{left:-15px;}
}

.redux-logo{
    width: 150px;
    position: relative;
    -webkit-animation-name: redux-logo;
            animation-name: redux-logo;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-fill-mode:forwards;
            animation-fill-mode:forwards;
}

@-webkit-keyframes redux-logo{
    0%{bottom:-100px;}
    100%{bottom:10px;}
}

@keyframes redux-logo{
    0%{bottom:-100px;}
    100%{bottom:10px;}
}

@-webkit-keyframes title{
    0% {top: -50px;}
    100%{top: 5px;}
}

@keyframes title{
    0% {top: -50px;}
    100%{top: 5px;}
}

.Home-logo {
    -webkit-animation: Home-logo-spin infinite 20s linear;
            animation: Home-logo-spin infinite 20s linear;
    width: 250px;
    margin-top: 20px;
    margin-bottom: 10px;
  }


  @media screen and (max-width: 420px) {
    .Home-logo {
        width: 150px;
        margin-bottom: 25px;
    }

    .react-logo{
        width: 90px;
        margin-bottom: 14px;
        margin-left: 15px;
    }
    
    .redux-logo{
        width: 96px;
    }

  }

  /* @media screen and (max-width: 256px) {
    .Home-logo {
        width: 130px;
        margin-bottom: 25px;
    }

    .react-logo{
        width: 90px;
    }
    
    .redux-logo{
        width: 90px;
    }

  } */

  @-webkit-keyframes Home-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }

  @keyframes Home-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
